.main-title {
  width: 250px;
  height: 33px;
  font-family: "Nunito Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 33px;
  text-align: center;
  padding-bottom: 15px;
  margin-left: 60px;
}

.modal-wrapper {
  border: 1px solid #2f5aa8;
  width: 350px;
  background: #ffffff;
  box-shadow: 10px 10px 50px rgba(0, 0, 0, 0.25);
  border-radius: 16px;
}
.modal-container {
  display: flex;
  justify-content: center;
  /* margin-top: 50px    ; */
  background-color: rgba(0, 0, 0, 0.2);
  /* background-attachment:fixed; */
  width: 100%;
  height: 550px;
  z-index: 999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.button-auth {
  background-color: #2f5aa8;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  gap: 10px;
  border-radius: 100px;
  width: 325px;
  color: white;
  border: 1px #2f5aa8;
  font-family: "Nunito Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 22px;
  cursor: pointer;
}
.button-auth:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px -10px rgba(24, 90, 219, 0.6);
  transition: ease-in-out;
}

.input-email {
  background: #f7f7fd;
  border: 1px solid #2f5aa8;
  border-radius: 8px;
  width: 280px;
  height: 22px;
  padding: 16px 20px;
}

.input-password {
  background: #f7f7fd;
  border: 1px solid #2f5aa8;
  border-radius: 8px;
  width: 280px;
  height: 22px;
  padding: 16px 20px;
}
.forget-container {
  padding-bottom: 30px;
  padding-top: 10px;
  margin-left: 220px;
}
.button-container {
  padding-bottom: 40px;
  margin-left: 13px;
}
.auth-container {
  padding-bottom: 20px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
}
.email-container {
  margin-left: 13px;
}
.password-container {
  margin-left: 13px;
}
.account-container {
  display: flex;
  justify-content: center;
  padding-bottom: 15px;
}
.cancel-button {
  margin-left: 310px;
  cursor: pointer;
  font-weight: 500;
  padding: 11px;
  border-radius: 12px;
  font-size: 0.8rem;
  border: none;
  color: #2c3e50;
  /* background: #fcfcfc; */
  transition: all 0.25s ease;
}
.email-name {
  margin-right: 180px;
}
.password-name {
  margin-right: 260px;
}
